/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.flex {
  display: flex;
  gap: var(--gap, 1rem);
  align-items: center;
}

.grid {
  display: grid;
  gap: var(--gap, 1rem);
}

.container {
  padding: 0 2em;
  padding-inline: 2em;
  max-width: 80rem;
  margin: 0 auto;
  margin-inline: auto;
}

.sr--only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background-color: var(--dark-bg);
  color: var(--dark-text2);
  position: relative;
}

.container {
  max-width: 75rem;
  padding: 1.25rem 2.5rem 0;
  margin: 0 auto;
}
@media (max-width: 39.937em) {
  .container {
    padding: 1.25rem 2.5rem 0;
  }
}

:root {
  --font-inter: "Inter", sans-serif;
}

p {
  margin-bottom: 1rem;
}

h1 {
  margin: 0;
  font-size: 1.383rem;
}

h2 {
  font-size: 1.296rem;
}

h3 {
  font-size: 1.215rem;
}

h4 {
  font-size: 1.138rem;
}

h5 {
  font-size: 1.067rem;
}

small,
.text_small {
  font-size: 0.937rem;
}

@media (min-width: 69.375em) {
  h1 {
    margin-top: 0;
    font-size: 1.802rem;
  }

  h2 {
    font-size: 1.602rem;
  }

  h3 {
    font-size: 1.424rem;
  }

  h4 {
    font-size: 1.266rem;
  }

  h5 {
    font-size: 1.125rem;
  }

  small,
.text_small {
    font-size: 0.889rem;
  }
}
:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(200, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-light: hsl(230, 22%, 74%);
  --toggle-dark: linear-gradient(
    225deg,
    hsl(210, 78%, 56) 0%,
    hsl(146, 68%, 55%) 98.9%
  );
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --dark-down: #dc414c;
  --dark-up: green;
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-text1: hsl(228, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

main {
  position: relative;
  padding-bottom: 3rem;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0.75rem auto;
}
.header__title {
  font-weight: 600;
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--dark-text1);
}

.card__container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.875rem;
  justify-content: center;
}
@media (max-width: 69.3125em) {
  .card__container {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 39.937em) {
  .card__container {
    grid-template-columns: repeat(1, 1fr);
  }
}
.card__container .card {
  position: relative;
  cursor: pointer;
  border-radius: 0.3125rem;
  text-align: center;
  padding: 1.25rem;
  width: 100%;
  background-color: var(--dark-card);
}
.card__container .card:hover {
  filter: brightness(150%);
  transition: background-color 0.2s ease-in-out;
}
.card__container .card__facebook {
  border-top: 0.3125rem solid var(--facebook);
}
.card__container .card__youtube {
  border-top: 0.3125rem solid var(--youtube);
}
.card__container .card__twitter {
  border-top: 0.3125rem solid var(--twitter);
}
.card__container .card__instagram {
  position: relative;
  overflow: hidden;
}
.card__container .card__instagram::before {
  content: "";
  position: absolute;
  top: 0rem;
  left: 0;
  width: 100%;
  height: 0.3125rem;
  background: linear-gradient(225deg, var(--instagram-end), var(--instagram-middle) 50.91%, var(--instagram-start) 100%);
  border-radius: 0.3125rem;
}
.card__container .card__instagram .card__platform {
  margin-top: 0.3125rem;
}
.card__container .card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.25rem;
}
.card__container .card__platform--icon {
  margin-right: 0.5625rem;
}
.card__container .card__platform--username {
  text-align: center;
  font-size: 0.75rem;
  color: var(--dark-text1);
}
.card__container .card__followers {
  margin-bottom: 1.25rem;
}
.card__container .card__followers--count {
  font-weight: 700;
  font-size: 3.125rem;
}
.card__container .card__followers--title {
  font-size: 0.75rem;
  color: var(--dark-text1);
  letter-spacing: 0.3125rem;
  text-transform: uppercase;
}
.card__container .card__date {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.75rem;
  font-weight: 600;
}
.card__container .card__date--number {
  margin-right: 0.4375rem;
}
.card__container .card__date--number svg {
  fill: var(--limegreen);
}
.card__container .card__date--today {
  color: var(--dark-up);
}
.card__container .card__date .down {
  color: var(--dark-down);
}
.card__container .card__sub {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  padding-top: 1.375rem;
}
.card__container .card__sub .card__username {
  font-size: 0.875rem;
  justify-self: flex-start;
  align-self: flex-start;
  color: var(--dark-text1);
}
.card__container .card__sub .card__icon {
  justify-self: flex-end;
}
.card__container .card__sub .card__count {
  font-size: 1.75rem;
  font-weight: 700;
  justify-self: flex-start;
}
.card__container .card__sub .card__date {
  justify-self: flex-end;
}
.card__container .card__sub .card__date img {
  margin-right: 0.1875rem;
  margin-top: 0.1875rem;
}

.subtitle {
  font-size: 1.25rem;
  margin: 0.5rem 0 1rem;
  text-align: left;
}
@media (max-width: 69.3125em) {
  .subtitle {
    margin-bottom: 1.25rem;
  }
}
@media (max-width: 39.937em) {
  .subtitle {
    text-align: left;
    max-width: 250px;
  }
}

.card__container--sub {
  row-gap: 1.125rem;
}

footer {
  width: 100%;
  position: absolute;
  text-align: center;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}
footer span {
  font-size: 0.6875rem;
}
footer span a {
  color: var(--dark-text1);
}

.toggle {
  border: none;
  display: flex;
  flex: 1;
  align-items: center;
  height: 1.5rem;
  justify-content: end;
}
.toggle-text {
  margin-right: 0.5rem;
}
.toggle .theme-switcher {
  position: relative;
  width: 53px;
  height: 24px;
  cursor: pointer;
}
.toggle .theme-switcher #dark {
  opacity: 0;
  width: 0;
  height: 0;
}
.toggle__background {
  display: block;
  border-radius: 0.75rem;
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(225deg, #378fe6 0%, #3eda82 98.9%);
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  height: 1.125rem;
  width: 1.125rem;
  display: block;
  border-radius: 50%;
  background: var(--dark-bg);
  transition: all 0.3s ease-out;
}
.toggle #dark:checked ~ .toggle__button {
  transform: translateX(28px);
}