.flex {
  display: flex;
  gap: var(--gap, 1rem);
  align-items: center;
}

.grid {
  display: grid;
  gap: var(--gap, 1rem);
}
.container {
  padding: 0 2em;
  padding-inline: 2em; //similar to ^
  max-width: 80rem;
  margin: 0 auto;
  margin-inline: auto;
}

.sr--only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap; //added line
  border: 0;
}
